/*  page  */
.tWaste-page{
  height: 100%;
  width: 100%;
  position: relative;

  background: black;
  --headBg:transparent;
  --headTxt:#fff;
  --headHL:red;
  --headAnm:red;
  --footBg:transparent;
  --footTxt:#4e6e8e;
  --footSub:#eaecef;
  --viewBg:transparent;
  --viewTxt:#FFFFFF;
  --viewAnm:#f7b3b3;
  --viewHL:red;
}
/*  header  */
.tWaste-header{
  position: fixed;
  /*position: absolute;*/
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  background: var(--headBg);
}
/*  header-logo  */
.tWaste-header-logo{
  font-size:4rem;
  height: 4rem;
  line-height: 4rem;
  margin-bottom: 1rem;
}
.icon-logo i{
  position: relative;
  cursor: pointer;
  opacity: 0;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.icon-logo i:nth-child(1){
  position: relative;
  color:var(--headHL);
  top: -2rem;
  opacity: 0.3;
}
.tWaste-header:hover i{
  top: 0;
  opacity: 0.8;
}
/*  header-sub*/
.icon-logo-version{
  position: relative;
  color: var(--headTxt);
  font-size: 2rem;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.tWaste-header:hover .icon-logo-version{
  opacity: 1;
  visibility: visible;
}
.icon-logo-version:before,.icon-logo-version:after{
  content: '';
  position: absolute;
  height: 0.25rem;
  width: 0%;
  bottom: -0.25rem;
  background-color: var(--headAnm);
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  opacity: 0.8;
  -webkit-animation: iconLogoLine 1s 0s ease-in-out infinite;
          animation: iconLogoLine 1s 0s ease-in-out infinite;
}
.icon-logo-version:before{
  left: 50%;
  text-align: right;
}
.icon-logo-version:after{
  right: 50%;
  text-align: right;
}
@-webkit-keyframes iconLogoLine {
  0%{
    width: 0%;
    opacity: 0.8;
  }
  100%{
    width: 45%;
    opacity: 0.1;
  }
}
@keyframes iconLogoLine {
  0%{
    width: 0%;
    opacity: 0.8;
  }
  100%{
    width: 45%;
    opacity: 0.1;
  }
}
.tWaste-header-btn{
  opacity: 0.8;
  color: var(--headTxt);
  -webkit-transition: 0.1s ease-in-out;
  transition: 0.1s ease-in-out;
}
.tWaste-header-btn i{
  margin: 0 1em;
  border-radius: 50%;
  overflow: hidden;
  -webkit-transition: 1s ease-in-out;
  transition: 1s ease-in-out;
}
.tWaste-header-btn:hover{
  color: var(--headHL);
  opacity: 1;
}
.tWaste-header-btn:hover i{
  -webkit-animation:btnWave 1s ease-in-out infinite;
          animation:btnWave 1s ease-in-out infinite;
}
@-webkit-keyframes btnWave {
  0%,100%{
    -webkit-box-shadow: 0 0 var(--headAnm);
            box-shadow: 0 0 var(--headAnm);
  }
  50%{
    -webkit-box-shadow: 0 0 0 0.5rem rgba(255, 255, 255, 0.3);
            box-shadow: 0 0 0 0.5rem rgba(255, 255, 255, 0.3);
  }
}
@keyframes btnWave {
  0%,100%{
    -webkit-box-shadow: 0 0 var(--headAnm);
            box-shadow: 0 0 var(--headAnm);
  }
  50%{
    -webkit-box-shadow: 0 0 0 0.5rem rgba(255, 255, 255, 0.3);
            box-shadow: 0 0 0 0.5rem rgba(255, 255, 255, 0.3);
  }
}
/*  views */
.tWaste-views{
  height: 100%;
  width: 100%;
  padding: 9rem 0 3rem 0;
  background: var(--viewBg);
  color: var(--viewTxt);
}
.tWaste-box{
  height: 100%;
  width: 100%;
  position: relative;
}
.tWaste-empty{
  height: 100vh;
  width: 100vw;
  position: relative;
}
/*  footer  */
.tWaste-footer{
  position: fixed;
  bottom: 0;
  height: calc(3rem - 1px);
  line-height: 3rem;
  width: 100%;
  text-align: center;
  border-top: 1px solid var(--footSub);
  background: var(--footBg);
  color: var(--footTxt);
}
/* -- common -- */
.vodal,
.vodal-mask {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}
.vodal {
    position: fixed;
}
.vodal-mask {
    position: absolute;
    background: rgba(0, 0, 0, .3);
}
.vodal-dialog {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 101;
    padding: 15px;
    background: #fff;
    border-radius: 3px;
    -webkit-box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
            box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
}
.vodal-close {
    position: absolute;
    cursor: pointer;
    top: 16px;
    right: 16px;
    width: 16px;
    height: 16px;
    z-index: 9999;
}
.vodal-close:before,
.vodal-close:after {
    position: absolute;
    content: '';
    height: 2px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #999;
    border-radius: 100%;
    -webkit-transition: background .2s;
    transition: background .2s;
}
.vodal-close:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.vodal-close:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.vodal-close:hover:before,
.vodal-close:hover:after {
    background: #333;
}
/* -- fade -- */
@-webkit-keyframes vodal-fade-enter {
    from {
        opacity: 0;
    }
}
@keyframes vodal-fade-enter {
    from {
        opacity: 0;
    }
}
.vodal-fade-enter-active {
    -webkit-animation: vodal-fade-enter both ease-in;
    animation: vodal-fade-enter both ease-in;
}
@-webkit-keyframes vodal-fade-leave {
    to {
        opacity: 0
    }
}
@keyframes vodal-fade-leave {
    to {
        opacity: 0
    }
}
.vodal-fade-leave-active {
    -webkit-animation: vodal-fade-leave both ease-out;
    animation: vodal-fade-leave both ease-out;
}
/* -- slideDown -- */
@-webkit-keyframes vodal-slideDown-enter {
    from {
        -webkit-transform: translate3d(0, -100px, 0);
        transform: translate3d(0, -100px, 0);
    }
}
@keyframes vodal-slideDown-enter {
    from {
        -webkit-transform: translate3d(0, -100px, 0);
        transform: translate3d(0, -100px, 0);
    }
}
.vodal-slideDown-enter-active {
    -webkit-animation: vodal-slideDown-enter both cubic-bezier(0.4, 0, 0, 1.5);
    animation: vodal-slideDown-enter both cubic-bezier(0.4, 0, 0, 1.5);
}
@-webkit-keyframes vodal-slideDown-leave {
    to {
        -webkit-transform: translate3d(0, -100px, 0);
        transform: translate3d(0, -100px, 0);
    }
}
@keyframes vodal-slideDown-leave {
    to {
        -webkit-transform: translate3d(0, -100px, 0);
        transform: translate3d(0, -100px, 0);
    }
}
.vodal-slideDown-leave-active {
    -webkit-animation: vodal-slideDown-leave both;
    animation: vodal-slideDown-leave both;
}
